<template>
  <div class="wrapper">
    <side-bar>
      <template slot="links">
        <sidebar-link @click="emptyMapStepper" to="/overview">
          <img
            v-show="getCurrentRoute === 'Overview'"
            class="custom-icon"
            src="@/assets/havr-icon/overview-active.svg"
          >
          <img
            v-show="getCurrentRoute !== 'Overview'"
            class="custom-icon"
            src="@/assets/havr-icon/overview.svg"
          >
          <p class="custom-label" :class="{'active-link': getCurrentRoute === 'Overview'}">Overview</p>
        </sidebar-link>

        <sidebar-link @click="emptyMapStepper" to="/keychain">
          <img
            class="custom-icon"
            v-show="getCurrentRoute === 'Keychain' || getCurrentRoute === 'Keychain-map-list' || getCurrentRoute === 'Keychain-map-edit'
            || getCurrentRoute === 'Keychain-map' || getCurrentRoute === 'Keychain-map-details'"
            src="@/assets/havr-icon/keychain-active.svg"
          >
          <img
            class="custom-icon"
            v-show="getCurrentRoute !== 'Keychain' && getCurrentRoute !== 'Keychain-map-list' && getCurrentRoute !== 'Keychain-map-edit' && getCurrentRoute !== 'Keychain-map' && getCurrentRoute !== 'Keychain-map-details'"
            src="@/assets/havr-icon/keychain.svg"
          >
          <p
            class="custom-label"
            :class="{'active-link': getCurrentRoute === 'Keychain' || getCurrentRoute === 'Keychain-map-list' || getCurrentRoute === 'Keychain-map-edit' || getCurrentRoute === 'Keychain-map' || getCurrentRoute === 'Keychain-map-details'}"
          >Keychain</p>
        </sidebar-link>

        <sidebar-link @click="emptyMapStepper" to="/users">
          <img
            class="custom-icon"
            v-show="getCurrentRoute === 'Users'"
            src="@/assets/havr-icon/users-active.svg"
          >
          <img
            class="custom-icon"
            v-show="getCurrentRoute !== 'Users'"
            src="@/assets/havr-icon/users.svg"
          >
          <p class="custom-label" :class="{'active-link': getCurrentRoute === 'Users'}">Users</p>
        </sidebar-link>
      </template>
      <mobile-menu>
        <li class="divider"></li>
      </mobile-menu>
    </side-bar>
    <div class="main-panel" :class="{'overview-content': getCurrentRoute === 'Overview'}">
      <top-navbar v-if="getCurrentRoute !== 'Keychain-map-list'"></top-navbar>
      <TopNavBarMapList v-if="getCurrentRoute === 'Keychain-map-list'"/>
      <TopNavbarDetailsGroup v-if="getCurrentRoute === 'Keychain-map-details'"/>
      <TopNavbarEditFloorMaps v-if="getCurrentRoute === 'Keychain-map-edit'"/>
      <dashboard-content @click.native="toggleSidebar"></dashboard-content>
      <content-footer v-if="getCurrentRoute === 'Keychain'"></content-footer>
      <user-footer v-if="getCurrentRoute === 'Users'"></user-footer>
    </div>
  </div>
</template>
<script>
import store from "@/flulux/store";
import TopNavbar from "./TopNavbar.vue";
import ContentFooter from "./ContentFooter.vue";
import UserFooter from "./UserTableFooter";
import DashboardContent from "./Content.vue";
import TopNavbarEditFloorMaps from "./TopNavbarEditFloorMap.vue";
import MobileMenu from "./MobileMenu";
import TopNavbarDetailsGroup from "./TopNavbarDetailsGroup";
import TopNavBarMapList from "./TopNavBarMaps";
import { dataRepository } from "@/repository/login";
export default {
  data() {
    return {
      show: true,
      store: store
    };
  },
  components: {
    TopNavbar,
    ContentFooter,
    DashboardContent,
    MobileMenu,
    UserFooter,
    TopNavbarDetailsGroup,
    TopNavBarMapList,
    TopNavbarEditFloorMaps
  },
  computed: {
    getCurrentRoute() {
      return this.$route.name;
    }
  },
  methods: {
    toggleSidebar() {
      if (this.$sidebar.showSidebar) {
        this.$sidebar.displaySidebar(false);
      }
    },
    emptyMapStepper() {
      store.state.map.floor_map.picture = null;
      store.state.map.floor_map.name = null;
      store.state.map.floor_map.tutorial = false;
      store.state.map.drawer = false;
    }
  }
};
</script>

<style>
.overview-content {
  background: #fafafa !important;
}
.custom-icon {
  font-size: 22px;
  float: left;
  margin-right: 15px;
  line-height: 30px;
  width: 22px;
  position: relative;
  top: 2px;
  text-align: center;
}

.custom-icon path {
  fill: #000;
}

.active-link {
  color: black !important;
}
.custom-label {
  margin: 0;
  line-height: 30px;
  font-size: 17px;
  color: #a6a6a6;
}
</style>
