<template>
  <div>
    <img
      @click="refreshForm"
      class="close-account-page"
      src="@/assets/havr-icon/close-drawer-icon.svg"
    >
    <div class="form-stepper-new-contact">
      <div class="jumbo">
        <h2>
          <b>
            <span class="step">{{step}} / 4</span>
            {{title}}
          </b>
        </h2>
        <p></p>
      </div>
      <br>

      <keys v-if="step === 1"/>
      <users v-if="step === 2"/>
      <rights v-if="step === 3"/>
      <access-details v-if="step === 4 && !isSharing"/>
      <img class="side-brand" src="@/assets/img/havr-side-brand.svg">
      <footer class="footer fixed">
        <notifications class="alert-info" group="foo"/>
        <div class="container-fluid d-flex flex-wrap justify-content-between">
          <nav>
            <ul>
              <li class="button-share-container">
                <button
                  @click="step--"
                  v-if="step > 1"
                  class="btn btn-primary outline back-button"
                >Back</button>
                <button
                  v-if="step===1"
                  :disabled="store.state.shareKey.lock_list_ids.length === 0"
                  @click="step++"
                  class="btn btn-primary havr-color"
                >Next</button>
                <button
                  :disabled="store.state.shareKey.user_list_ids.length === 0"
                  v-if="step===2"
                  @click="step++"
                  class="btn btn-primary havr-color"
                >Next</button>
                <button v-if="step===3" @click="step++" class="btn btn-primary havr-color">Next</button>
                <button
                  :disabled="isSharing"
                  v-if="step===4"
                  @click="shareKey"
                  class="btn btn-primary havr-color"
                >
                  Share
                  <div class="align-button-share" v-if="isSharing">
                    <vue-lottie
                      class="loading-circle"
                      :data="loadingAnimation"
                      loop
                      autoplay
                      ref="lottieDark"
                    ></vue-lottie>
                  </div>
                </button>
              </li>
            </ul>
          </nav>
          <div class="copyright d-flex flex-wrap"></div>
        </div>
      </footer>
    </div>
  </div>
</template>



<script>
import repository from "@/repository/users";
import contactRepository from "@/repository/contact";
import keychainRepository from "@/repository/keychain";
import AccessDetails from "@/pages/shareKey/stepperForm/AccessDetails";
import Users from "@/pages/shareKey/stepperForm/Users";
import Rights from "@/pages/shareKey/stepperForm/Rights";
import vueLottie from "vue-lottie-ssr";
import loadingAnimation from "@/assets/lottie/loading_onlight.json";

import Keys from "@/pages/shareKey/stepperForm/Keys";
import store from "@/flulux/store";
import moment from "moment";
export default {
  data() {
    return {
      loadingAnimation,
      step: 1,
      store: store,
      end_copy: null,
      start_copy: null,
      isSharing: false,
      form: {
        start_date: moment().format(),
        end_date: null,
        start_on_demand: null,
        end__on_demand: null,
        admin: false,
        recurrency: "none",
        on_demand: false,
        lock_group_ids: [],
        user_list_ids: [],
        lock_list_ids: [],
        shallowCopy: []
      }
    };
  },
  components: {
    Users,
    Rights,
    Keys,
    AccessDetails,
    vueLottie
  },
  computed: {
    title() {
      if (this.step === 1) return "What key(s) do you want to share";
      if (this.step === 2) return "Who will be the users ?";
      if (this.step === 3) return "What rights should they have ?";
      if (this.step === 4) return "Access details";
    },
    lottieDark() {
      return this.$refs.lottieDark;
    }
  },
  mounted() {
    if (!window.sessionStorage.getItem("token")) {
      return this.$router.push("login");
    }
    window.onbeforeunload = async () => {
      window.setTimeout(async () => {
        const res = await this.store.actions.REFRESH_SHARE_KEY_FORM(this.form);
        this.$router.go(-1);
      }, 0);
      window.onbeforeunload = null;
    };
  },
  methods: {
    async shareKey() {
      this.isSharing = true;
      delete this.store.state.shareKey.shallowCopy;
      this.start_copy = this.store.state.shareKey.start_date;
      this.end_copy = this.store.state.shareKey.end_date;
      this.store.state.shareKey.start_date =
        moment(this.store.state.shareKey.start_date).format(
          "YYYY-MM-DDTHH:mm"
        ) + ":00Z";
      if (this.store.state.shareKey.end_date)
        this.store.state.shareKey.end_date =
          moment(this.store.state.shareKey.end_date).format(
            "YYYY-MM-DDTHH:mm"
          ) + ":00Z";
      const post = await keychainRepository.shareKey(this.store.state.shareKey);
      if (post === 201) {
        this.notifyShare("success", "Keys has been shared with success");
        this.refreshForm();
        this.isSharing = false;
        window.location.href = "/#/keychain";
      } else {
        this.notifyShare(
          "danger",
          "Internal error during the sharing key process, please retry later."
        );
        this.store.state.shareKey.start_date = this.start_copy;
        if (this.store.state.shareKey.end_date)
          this.store.state.shareKey.end_date = this.end_copy;
      }

      this.isSharing = false;
    },
    async refreshForm() {
      const res = await this.store.actions.REFRESH_SHARE_KEY_FORM(this.form);
      this.$router.push("/keychain");
    },
    notifyShare(type, message) {
      this.$notify({
        message: message,
        type: type,
        horizontalAlign: "right",
        timeout: 3000,
        verticalAlign: "top"
      });
    }
  }
};
</script>


