<template>
  <div :class="{'nav-open': $sidebar.showSidebar}">
    <notifications group="foo"/>
    <router-view></router-view>
    <div v-if="store.state.user.isConnected || intercom " style="display:hidden">{{initIntercom()}}</div>
  </div>
</template>

<script>
import store from "@/flulux/store";
import { setTimeout } from "timers";
export default {
  data() {
    return {
      store,
      intercom: sessionStorage.getItem("intercom")
    };
  },
  methods: {
    initIntercom() {
      const currentUser = JSON.parse(sessionStorage.getItem("currentUser"));
      setTimeout(
        () =>
          this.$intercom.boot({
            user_id: currentUser.id,
            name: currentUser.first_name + " " + currentUser.last_name,
            email: currentUser.email
          }),
        600
      );
    }
  }
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.vdatetime-popup__header {
  padding: 15px 30px;
  color: white !important;
  font-size: 32px;
  background: #111111 !important;
}

.vdatetime-popup__actions__button {
  display: inline-block;
  border: none;
  padding: 10px 20px;
  background: transparent;
  font-size: 16px;
  color: #919098 !important;
  cursor: pointer;
  transition: color 0.3s;
}

.vdatetime-calendar__month__day--selected > span > span,
.vdatetime-calendar__month__day--selected:hover > span > span {
  color: #fff !important;
  background: #919098 !important;
}
</style>
