<template>
  <div class="loading-screen">
    <img src="@/assets/havr-icon/loading_logo.svg">
    <p>Loading data...</p>
  </div>
</template>

<script>
import keychainRepository from "@/repository/keychain";
import usersRepository from "@/repository/users";
import loginRepository from "@/repository/login";
import moment from "moment";
import mapRepository from "@/repository/map";
import usersMixin from "@/mixins/users";
import { mapDataRepository } from "@/repository/map";
import { userDataRepository } from "@/repository/users";
export default {
  async mounted() {
    await this.getKeychain();
    await this.getUsers();
    await this.getAllFloorMaps();
    this.redirectUserFromLocation();
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      sessionStorage.setItem("from", from.name);
    });
  },
  methods: {
    async getKeychain() {
      const access = {
        client_id: "web_plateform_client_id",
        user_agent: navigator.userAgent,
        grant_type: "refresh_token",
        refresh_token: sessionStorage.getItem("refresh_token")
      };
      await loginRepository.refreshToken(access);
      await Promise.all([
        keychainRepository.getDoorLocks(),
        keychainRepository.getSharedK()
      ]);
    },
    async getUsers() {
      await usersRepository.retrieveContacts();
      await usersMixin.parseDataTable(userDataRepository.contacts);
    },
    async getAllFloorMaps() {
      const prevlocation = sessionStorage.getItem("from");
      if (prevlocation === "Keychain-map" || "Keychain-map-edit") {
        mapDataRepository.floor_maps = [];
      }
      const maps = await mapRepository.getAllFloorMaps();

      if (maps.length === 0) return (mapDataRepository.floor_maps = null);
      await Promise.all(
        maps.map(async item => {
          const details = await mapRepository.getOneFloorMaps(item.id);
          mapDataRepository.floor_maps.push({
            ...item,
            markers: JSON.parse(item.positions),
            picture: details.base64_picture,
            showHover: false
          });
        })
      );
    },
    redirectUserFromLocation() {
      const prevlocation = sessionStorage.getItem("from");
      if (prevlocation === "undefined") {
        return this.$router.push("keychain");
      }
      if (
        prevlocation === "Keychain-map" ||
        prevlocation === "Keychain-map-edit"
      ) {
        return this.$router.push("Keychain-map-list");
      }
      this.$router.push(prevlocation);
    }
  }
};
</script>


<style>
.loading-screen {
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-50%, -50%);
}
</style>

